


.timeline {
    position: relative;
    padding-left: 30px;
    margin-top: 20px;
}

.timeline-item {
    margin-bottom: 20px;
    position: relative;
}

.timeline-dot {
    position: absolute;
    left: -15px;
    top: 5px;
    width: 12px;
    height: 12px;
    background-color: #007bff;
    border-radius: 50%;
}

